import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import useSummaryContent from '../context/SummaryContext'
export default function About() {
  const { pathname } = useLocation();
  const isFull = pathname === '/about';
  const template = useSummaryContent();

  return (
    <section id="about" className="bg-white dark:bg-gray-900">
      <div className={template.container}>
        <div className={template.inner + " border-b pb-8"}>
          <h2 className={template.title}>
           About us
          </h2>
          <div className="mt-4">
            <span className="block text-black lg font-semibold text-black-900 dark:text-black">Many Years of experience</span>
            <h3 className="mt-2 text-black 3xl font-extrabold text-black-900 dark:text-black">
              Specialized provider of implementation solutions for healthcare business processes
            </h3>
            <p className="mt-4 text-black-500 dark:text-black-400">
              Juxt Rx is a specialized provider of implementation solutions for healthcare business processes...
            </p>
            <p className="mt-4 text-black-500 dark:text-black-400">
              Whether you need help with patient registration, medical billing, or other healthcare processes, our team is here to help.
            </p>
          </div>
          {!isFull && (
            <div className="mt-6">
              <Link
                to="/about"
                className={template.button}
              >
                Read More
              </Link>
            </div>
          )}
        </div>

        {/* Conditional Full Content Section */}
        {isFull && (
          <div className="mt-8">
            {/* Here you can apply a different template or custom classes for the full content */}
            <div className="max-w-screen-md mx-auto text-black">
              <span className="block text-black font-bold text-black-900 dark:text-black">
                Our Value Addition
              </span>
              <hr className="my-4 border-gray-200 dark:border-gray-700" />
              <h3 className="text-black3xl font-extrabold text-black-900 dark:text-black">
                We offer various value addition as listed below
              </h3>
              <p className="mt-4 text-black-500 dark:text-black-400">
                At Juxt Rx, we pride ourselves on delivering comprehensive and customized solutions...
              </p>
              <div className="mt-8">
                <button className={template.button}>
                  Explore More
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </section>
  );
}
