import React from 'react';
import { Link } from 'react-router-dom';
import { ChevronRight } from 'lucide-react';
import useSummaryContent from '../context/SummaryContext'

const Home = () => {
  const template = useSummaryContent();

  return (
    <>
      <header className="py-4 px-4">
        <div className="flex justify-between items-center">
          <div>
            <a href="https://juxtrx.ke/">
              <img
                src="https://juxtrx.ke/logo-262x142.png"
                alt="Juxtrx Logo"
                className="h-[50px]" // Using Tailwind class instead of inline style 
              />
            </a>
          </div>
          <ul className="flex space-x-4">
            <li>
              <a 
                href="mailto:info@juxtrx.ke"
                className="hover:text-primary-600 transition-colors"
              >
                info@juxtrx.ke
              </a>
            </li>
            <li>
              <p className="text-black-600 dark:text-black-400">
                Sun - Fri: 8.00am - 10.00pm
              </p>
            </li>
          </ul>
        </div>
      </header>

      <section id="home" className="bg-white dark:bg-black-900">
        <div className={template.container}>
          <div className={template.inner}>
            <p className="mb-2">Welcome to Juxtrx.</p>
            <h2 className={template.title}>
              Pharmacy Operations, and Consultancy Services
            </h2>
            <p className={template.description}>
              In addition to our core services, Juxt Rx extends its expertise to pharmacy operations, Pharmacy Benefit Management (PBM), and consultancy services.
            </p>
            <Link to="/about" className={template.button}>
              Discover Now
              <ChevronRight className="w-4 h-4" />
            </Link>
          </div>
        </div>
      </section>
    </>
  );
};

export default Home;
