import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import useSummaryContent from '../context/SummaryContext'


export default function Partner() {
  const { pathname } = useLocation();
  const isFull = pathname === '/partner';
  const template = useSummaryContent();


  return (
    <section id="partner" className="bg-white dark:bg-gray-900">
    <div className={template.container}>
      <div className={template.inner + " border-b pb-8"}>
        <h2 className={template.title}>Our Partners</h2>
        <div className="mt-4 text-gray-500 dark:text-gray-400">
          <p>Nourish and Bloom Pharmacy</p>
        </div>
        {!isFull && (
          <div className="mt-6">
            <Link to="/partner" className={template.button}>
              See More
            </Link>
          </div>
        )}
      </div>

        {isFull && (
          <div className="mt-8">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
              {/* First Column */}
              <div>
                <h2 className="text-xl font-bold text-gray-900 dark:text-white">
                  <a href="https://juxtrx.ke/" className="hover:underline">
                    Nourish &amp; Bloom Pharmacy - Embrace a holistic wellness journey with our exclusive offerings
                  </a>
                </h2>
                <p className="mt-4 text-gray-500 dark:text-gray-400">
                  As a seasoned professional in the healthcare industry, I am thrilled to unveil the unparalleled value and offerings that Nourish &amp; Bloom Pharmacy brings to your well-being. We have carefully crafted our services with creativity and ingenuity, focusing on nourishing your life through a holistic approach to wellness.
                </p>
                <p className="mt-4 text-gray-500 dark:text-gray-400">
                  At Nourish &amp; Bloom Pharmacy, we go beyond the conventional pharmacy experience. Our curated selection of high-quality health and wellness products caters to every aspect of your well-being, from premium prescription medications to natural beauty essentials. Step into a world of enriched pharmacy services, empowering you to thrive both physically and spiritually.
                </p>
                <p className="mt-4 text-gray-500 dark:text-gray-400">
                  Our expert team of pharmacists and healthcare consultants are dedicated to personalizing your wellness journey. By offering specialized consultancy services, we streamline pharmacy operations, optimize medication management, and enhance patient safety.
                </p>
                <p className="mt-4 text-gray-500 dark:text-gray-400">
                  But our commitment doesn't end there. Embrace tranquility through our rejuvenating wellness services, ranging from mindfulness practices to specialized nutrition plans. Unveil your inner radiance with our handpicked natural beauty products, ensuring you glow from the inside out.
                </p>
                <p className="mt-4 text-gray-500 dark:text-gray-400">
                  Join us in our mission to nourish your body, mind, and soul. Together, let's embark on a transformative wellness journey that transcends the ordinary and embraces the extraordinary. Click the partner link above to explore the enriching world of Nourish &amp; Bloom Pharmacy, where your holistic well-being is our ultimate priority.
                </p>
              </div>

              {/* Second Column */}
              <div>
                <h2 className="text-xl font-bold text-gray-900 dark:text-white">
                  Nourish &amp; Bloom Pharmacy - Experience integrated pharmacy, wellness, and beauty solutions with Juxt Rx
                </h2>
                <p className="mt-4 text-gray-500 dark:text-gray-400">
                  Welcome to Nourish &amp; Bloom Pharmacy, where we believe in nourishing your life the natural way. We are excited to partner with Juxt Rx, a specialized provider of implementation solutions for healthcare business processes, to bring you an integrated experience that combines pharmacy expertise with cutting-edge solutions.
                </p>
                <p className="mt-4 text-gray-500 dark:text-gray-400">
                  Juxt Rx is dedicated to empowering healthcare organizations by delivering seamless and advanced solutions that optimize business processes, drive operational excellence, and elevate patient care. With a strong focus on pharmacy operations, pharmacy benefit management, consultancy services, and more, Juxt Rx is at the forefront of transforming the healthcare industry.
                </p>
                <p className="mt-4 text-gray-500 dark:text-gray-400">
                  By clicking the partner link above, you will be directed to an integrated page where you can explore the synergy between Nourish &amp; Bloom Pharmacy and Juxt Rx. Together, we offer comprehensive value-added services that enhance pharmacy operations, streamline medication management, and optimize patient safety.
                </p>
                <p className="mt-4 text-gray-500 dark:text-gray-400">
                  Through Juxt Rx's expertise in pharmacy benefit management (PBM), we ensure cost containment and quality care by negotiating the best pricing for medications while ensuring a wide range of drug options for plan members. Additionally, Juxt Rx provides consultancy services to healthcare organizations, offering valuable insights and strategic recommendations to drive positive outcomes and business growth.
                </p>
                <p className="mt-4 text-gray-500 dark:text-gray-400">
                  Juxt Rx's focus on local trade representation and brand management complements our commitment to delivering quality products. Their on-the-ground support and market intelligence help pharmaceutical companies expand their market reach and establish a strong brand identity.
                </p>
                <p className="mt-4 text-gray-500 dark:text-gray-400">
                  Navigating the regulatory landscape can be challenging, but Juxt Rx offers regulatory support services to streamline the process of obtaining approvals, certifications, and product registrations. This ensures a smooth market entry for pharmaceutical companies, enabling them to bring their products to market efficiently and in compliance with local regulations.
                </p>
                <p className="mt-4 text-gray-500 dark:text-gray-400">
                  For entrepreneurs and startups entering the healthcare industry, Juxt Rx provides specialized startup services that offer guidance, market analysis, and business planning support. Whether you're launching a new pharmacy, a telehealth platform, or a healthcare-related technology solution, Juxt Rx's startup services lay the foundation for success in the dynamic healthcare market.
                </p>
                <p className="mt-4 text-gray-500 dark:text-gray-400">
                  At Nourish &amp; Bloom Pharmacy, we are proud to collaborate with Juxt Rx to bring you comprehensive and customized solutions. Together, we are committed to transforming healthcare businesses and driving positive outcomes for our clients.
                </p>
              </div>
            </div>
          </div>
        )}
      </div>
    </section>
  );
}