import { useMemo } from 'react';

export default function useUnconditionalTemplate() {
 
  return useMemo(() => ({
    container: "py-8 px-4 mx-auto max-w-screen-xl lg:py-16 lg:px-6",
    inner: "max-w-screen-md mx-auto text-center",
    title: "mb-4 text-4xl tracking-tight font-extrabold text-gray-900 dark:text-black",
    description: "mb-8 font-light text-gray-500 dark:text-gray-400 sm:text-xl",
    buttonWrapper: "flex justify-center",
    button: "inline-flex items-center justify-center px-5 py-3 text-base font-medium text-black bg-primary-600 rounded-lg hover:bg-primary-700 focus:ring-4 focus:outline-none focus:ring-primary-300 dark:focus:ring-primary-900"
  }), []);
}
