import React from 'react';

function Contact() {
  return (
    <section id="contact" className="bg-white dark:bg-gray-900">
      <div className="py-8 px-4 mx-auto max-w-screen-xl lg:py-16 lg:px-6">
        <div className="max-w-screen-md mx-auto">
          <h2 className="mb-4 text-4xl font-bold text-gray-900 dark:text-white">
            Write A Message
          </h2>
          <form className="space-y-4">
            <div>
              <input
                type="text"
                placeholder="Your Name"
                required
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-primary-500 focus:border-primary-500 dark:bg-gray-800 dark:border-gray-700 dark:placeholder-gray-400 dark:text-white"
              />
            </div>
            <div>
              <input
                type="email"
                placeholder="Your Email"
                required
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-primary-500 focus:border-primary-500 dark:bg-gray-800 dark:border-gray-700 dark:placeholder-gray-400 dark:text-white"
              />
            </div>
            <div>
              <input
                type="text"
                placeholder="Your Phone"
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-primary-500 focus:border-primary-500 dark:bg-gray-800 dark:border-gray-700 dark:placeholder-gray-400 dark:text-white"
              />
            </div>
            <div>
              <input
                type="text"
                placeholder="Select Date"
                required
                className="w-full p-3 border border-gray-300 rounded-lg focus:ring-primary-500 focus:border-primary-500 dark:bg-gray-800 dark:border-gray-700 dark:placeholder-gray-400 dark:text-white"
              />
            </div>
            <button
              type="submit"
              className="w-full inline-flex justify-center items-center px-5 py-3 text-white bg-primary-600 hover:bg-primary-700 focus:ring-4 focus:outline-none focus:ring-primary-300 font-medium rounded-lg"
            >
              <span>Confirm Appointment</span>
            </button>
          </form>
        </div>
      </div>
    </section>
  );
}

export default Contact;
