import React, { useState } from 'react';
  import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
  import InfiniteScroll from 'react-infinite-scroll-component';

  import Navbar from "./components/hamburger/Navbar";
  import Footer from "./pages/Footer";
  import About from "./pages/About";
  import Services from "./pages/Services";
  import Contact from "./pages/Contact";
  import Partner from "./pages/Partner";
  import Home from "./pages/Home";

  const HomePage = () => {
    const sections = [
      { component: Home, key: 'home' },
      { component: About, key: 'about' },
      { component: Services, key: 'services' },
      { component: Contact, key: 'contact' },
      { component: Partner, key: 'partner' },
      { component: Footer, key: 'footer' },
      
    ];

    const [visibleCount, setVisibleCount] = useState(2);

    const loadMoreSections = () => {
      setVisibleCount((prev) => Math.min(prev + 1, sections.length));
    };

    return (
      <InfiniteScroll
        dataLength={visibleCount}
        next={loadMoreSections}
        hasMore={visibleCount < sections.length}
        loader={<h4 className="text-center py-4">Loading...</h4>}
      >
        {sections.slice(0, visibleCount).map(({ component: Section, key }) => (
          <Section key={key} />
        ))}
      </InfiniteScroll>
    );
  };

  const App = () => {
    return (
      <Router>
        <Navbar />
        <Routes>
          <Route path="/" element={<HomePage />} />
          <Route path="/about" element={<About />} />
        <Route path="/services" element={<Services />} />
        <Route path="/partner" element={<Partner />} />
        <Route path="/contact" element={<Contact />} />
        </Routes>
        <Footer />
      </Router>
    );
  };

  export default App;