import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import useSummaryContent from '../context/SummaryContext'


const services = [
  {
    title: 'Cutting-Edge Pharmacy Benefits Management (PBM)',
    description:
      'Juxt Rx delivers comprehensive PBM services, leveraging advanced analytics and predictive modeling to optimize prescription drug benefits. Our solutions focus on cost containment, personalized care, and improved patient outcomes, demonstrating our potential for profitability to investors.',
    link: 'https://juxtrx.ke/',
  },
  {
    title: 'Future-Ready Market Entry and Brand Management',
    description:
      'Juxt Rx specializes in market entry support, brand management, and regulatory compliance. We navigate complex regulatory landscapes, streamline product registration processes, and create impactful marketing strategies that resonate with consumers. Investors recognize our expertise in expanding market presence and generating sustainable revenue streams.',
    link: 'https://juxtrx.ke/',
  },
  {
    title: 'Strategic Consultancy Services',
    description:
      "Juxt Rx provides strategic consultancy services to healthcare organizations. Our experienced consultants offer valuable insights, actionable strategies, and data-driven recommendations to drive growth, efficiency, and patient-centric care. Investors recognize the value we bring to healthcare organizations.",
    link: 'https://juxtrx.ke/',
  },
  {
    title: 'Next-Generation Pharmacy Operations',
    description:
      'Juxt Rx offers innovative implementation solutions that optimize pharmacy operations. We leverage advanced technologies, automation, and data analytics to streamline workflows, enhance inventory management, and ensure regulatory compliance, showcasing our potential for high returns to investors.',
    link: 'https://juxtrx.ke/',
  },
];

export default function Services() {
  // Determine whether the full services page is rendered.
  const { pathname } = useLocation();
  const isFull = pathname === '/services';
    const template = useSummaryContent();
  

  return (
    <section id="services" className="bg-white dark:bg-gray-900">
    <div className={template.container}>
      <div className={template.inner + " border-b pb-8"}>
        <h2 className={template.title}>Our Services</h2>
        <div className="mt-4 text-gray-500 dark:text-gray-400">
          <p>
            Service overview content always visible, describing our main offerings.
          </p>
        </div>
        <div className="mt-8">
          <div className="text-black dark:text-black">
            <p>
              Welcome to our comprehensive service offerings. We deliver innovative solutions that span across multiple domains.
            </p>
            <p className="mt-4">
              Our services include cutting-edge technology integration, process optimization, and strategic consulting to empower your business.
            </p>
            <p className="mt-4">
              Through our dedicated team and extensive industry expertise, we provide tailored solutions that drive measurable results and sustainable growth.
            </p>
            <p className="mt-4">
              Whether you're looking to enhance operational efficiency, embrace digital transformation, or launch new initiatives, our services are designed to help you succeed.
            </p>
            <p className="mt-4">
              We pride ourselves on delivering quality, innovation, and unparalleled customer support. Let us partner with you to achieve your business objectives.
            </p>
          </div>
          {/* Render a "See More" link when not on the full services page */}
          {!isFull && (
            <div className="mt-6">
              <Link to="/services" className={template.button}>
                See More
              </Link>
            </div>
          )}
        </div>


        {isFull && (
          <>
            {/* Detailed Services Grid Section */}
            <section id="service-detailed" className="bg-white py-24 sm:py-32">
              <div className="mx-auto max-w-7xl px-6 lg:px-8">
                <div className="mx-auto max-w-2xl lg:text-center">
                  <span className="text-indigo-600">Our Services</span>
                  <h2 className="text-base font-semibold text-indigo-600">
                    Value Addition - Pharmacy Operations, and Consultancy Services
                  </h2>
                  <p className="mt-6 text-lg text-black">
                    By focusing on innovation, integrity, client-centricity, collaboration, agility, sustainability, and delivering next-generation solutions, Juxt Rx attracts attention and appeals to investors seeking dynamic, forward-thinking business partners. Our services in pharmacy operations, PBM, and consultancy demonstrate our ability to create value, drive growth, and achieve sustainable success in the evolving healthcare landscape.
                  </p>
                </div>

                <div className="mx-auto mt-16 max-w-2xl sm:mt-20 lg:mt-24 lg:max-w-4xl">
                  <dl className="grid max-w-xl grid-cols-1 gap-x-8 gap-y-10 lg:max-w-none lg:grid-cols-2 lg:gap-y-16">
                    {services.map((service) => (
                      <div key={service.title} className="relative pl-16">
                        <dt className="text-base font-semibold text-black">
                          <h2>
                            <a href={service.link} className="hover:underline">
                              {service.title}
                            </a>
                          </h2>
                        </dt>
                        <dd className="mt-2 text-base text-black">
                          {service.description}
                        </dd>
                      </div>
                    ))}
                  </dl>
                </div>
              </div>
            </section>
            </>
          )}
        </div>
      </div>
    </section>
  );
}